import { createStore } from 'vuex'

export default createStore({
  state: {
    arrayParametros: [],
    arrayParametrosFull: [],
    arrayTiposEquipos: [],

    // SOLO DATOS DE CAPTURAS
    arrayHistoricoFull: [],

    arrayHistorico:
    {
      data: [],
      destinos: [],
      bitacoras: [],
      parametros: [],
      tiposEquipos: [],
      equipos: [],
    },
    conexion: false,
    contador: {
      hoy: 0,
      pendientes: 0,
      capturados: 0,
    },

    arrayGraficos: [],
  },

  mutations: {

    setArrayParametros(state, payload) {
      state.arrayParametros = payload;
    },

    setArrayParametrosFull(state, payload) {
      state.arrayParametrosFull = payload;
    },

    setArrayTiposEquipos(state, payload) {
      state.arrayTiposEquipos = payload;
    },

    setArrayHistorico(state, payload) {
      state.arrayHistorico.data = payload.data;
      state.arrayHistoricoFull = payload.data;

      const destinos = [];
      const tiposEquipos = [];
      const parametros = [];
      const bitacoras = [];
      const equipos = [];

      payload.data.forEach(x => {

        // DESTINOS
        if (!destinos.includes(x.destino.toUpperCase())) {
          destinos.push(x.destino.toUpperCase());
        }

        // TIPOS DE EQUIPOS
        if (!tiposEquipos.includes(x.tipoEquipo.toUpperCase())) {
          tiposEquipos.push(x.tipoEquipo.toUpperCase());
        }

        // EQUIPOS
        if (!equipos.includes(x.equipo.toUpperCase())) {
          equipos.push(x.equipo.toUpperCase());
        }

        // BITACORAS
        if (!bitacoras.includes(x.bitacora.toUpperCase())) {
          bitacoras.push(x.bitacora.toUpperCase());
        }

        // PARAMETROS
        if (!parametros.includes(x.parametro.toUpperCase())) {
          parametros.push(x.parametro.toUpperCase());
        }

      });

      // RETORNA VALORES
      state.arrayHistorico.destinos = destinos;
      state.arrayHistorico.tiposEquipos = tiposEquipos;
      state.arrayHistorico.bitacoras = bitacoras;
      state.arrayHistorico.parametros = parametros;
      state.arrayHistorico.equipos = equipos;


    },

    setArrayGraficos(state, payload) {
      state.arrayGraficos = payload.graficos;
    },

  },

  actions: {

    // OBTIENE DATOS DEL ARRAY
    async getArray({ commit }, array) {
      try {
        const res = await fetch(`../apis/bitacoras_parametros.php`, {
          method: "POST",
          body: JSON.stringify(array),
        });
        const data = await res.json();
        if (data.response == "SUCCESS") {
          localStorage.setItem("offline", JSON.stringify(data));
          commit("setArrayParametros", data.data);
          commit("setArrayParametrosFull", data.data);
          commit("setArrayTiposEquipos", data.tiposEquipos);

          return true;
        }

        return false;
      } catch (error) {
        console.error(error);
        return false;
      }
    },

    // COMPRUEBA LA CONEXIN
    async getConexion({ commit, state }, array) {
      try {
        const res = await fetch(`../apis/bitacoras_parametros.php`, {
          method: "POST",
          body: JSON.stringify(array),
        });
        const data = await res.json();
        if (data.response == "SUCCESS") {
          return await data.conexion;
        }
        return false;

      } catch (error) {
        const array = JSON.parse(localStorage.getItem("offline"));
        commit("setArrayParametros", array.data);
        commit("setArrayParametrosFull", array.data);
        commit("setArrayTiposEquipos", array.tiposEquipos);
        return false;
      }
    },

    // CAPTURA DATOS DE LA BITACORA
    async getCaptura({ commit, state }, array) {
      try {
        const res = await fetch(`../apis/bitacoras_parametros.php`, {
          method: "POST",
          body: JSON.stringify(array),
        });
        const data = await res.json();
        if (data.response == "SUCCESS") {
          return await data.conexion;
        }
        return false;
      } catch (error) {
        const array = JSON.parse(localStorage.getItem("offline"));
        array.data = state.arrayParametros;
        localStorage.setItem("offline", JSON.stringify(array));
        return false;
      }
    },

    // OBTIENE EL HISTORICO DE LAS CAPTURAS
    async getArrayHistorico({ commit }, array) {
      try {
        const res = await fetch(`../apis/bitacoras_parametros.php`, {
          method: "POST",
          body: JSON.stringify(array),
        });
        const data = await res.json();

        if (data.response == "SUCCESS") {
          commit("setArrayHistorico", data);
          return true;
        }

        return false;
      } catch (error) {
        console.log(error);
        return false;
      }
    },

    // OBTIENE EL ARRAY DE LOS GRAFICOS
    async getArrayGraficos({ commit }, array) {
      try {
        const res = await fetch(`../apis/bitacoras_parametros.php`, {
          method: "POST",
          body: JSON.stringify(array),
        });
        const data = await res.json();

        if (data.response == "SUCCESS") {
          commit("setArrayGraficos", data);
          return { response: true, data: data.data };
        }

        return { response: false, data: [] };
      } catch (error) {
        console.log(error);
        return { response: false, data: [] };
      }
    },

    // OBTIENE DATOS DEL ARRAY
    async accionesUsuario({ commit }, array) {
      try {
        const res = await fetch(`../apis/bitacoras_parametros.php`,
          {
            method: "POST",
            body: JSON.stringify(array),
          });
        const data = await res.json();
        if (data.response == "SUCCESS") {
          return { response: true, data: data };
        }
        return { response: false, data: [] };
      } catch (error) {
        console.log(error);
        return { response: false, data: [] };
      }
    },

    // OBTIENE DATOS DE LA SESIN
    async sesion({ commit }, array) {
      try {
        const res =
          await fetch(`../apis/inicio_sesion.php`, {
            method: "POST",
            body: JSON.stringify(array),
          });
        const data = await res.json();
        if (data.response == "SUCCESS") {
          return { response: true, data: data };
        }
        return { response: false, data: [] };
      } catch (error) {
        console.log(error);
        return { response: false, data: [] };
      }
    },



  },

  modules: {
  }
})
