<script>
import { defineComponent } from "vue";
import { Line } from "vue3-chart-v2";

export default defineComponent({
  name: "Grafico01",
  props: { array: { type: Object } },
  extends: Line,

  data() {
    const label = [];
    const limitesSuperiores = [];
    const valores = [];
    const limitesInferiores = [];

    this.array.data.forEach((item) => {
      label.push(item.fechaTokenCorta);
      limitesInferiores.push(item.parametroMinimo);
      limitesSuperiores.push(item.parametroMaximo);
    });

    return {
      state: {
        chartData: {
          labels: label,
          datasets: [
            {
              label: "Lmite Superior",
              data: limitesSuperiores,
              fill: false,
              borderColor: "rgb(254,119,123,0.7)",
              tension: 0.1,
            },
            {
              label: "Registro",
              data: valores,
              fill: false,
              borderColor: "rgb(5,101,240)",
              tension: 0.3,
            },
            {
              label: "Lmite Inferior",
              data: limitesInferiores,
              fill: false,
              borderColor: "rgb(253,230,138,0.7)",
              tension: 0.1,
            },
          ],
        },
        chartOptions: {
          responsive: true,
          maintainAspectRatio: false,
          title: {
            display: true,
            text: `${this.array.equipo} \n (${this.array.parametro})`,
          },
        },
      },
    };
  },
  mounted() {
    this.renderChart(this.state.chartData, this.state.chartOptions);
  },
});
</script>
