<template>
  <div
    class="
      w-full
      h-screen
      flex flex-col
      items-center
      justify-start
      px-2
      py-1.5
      md:py-3
    "
  >
    <div class="md:py-3 leading-none">
      <h1 class="fw-700 text-xl">Registros Histrico</h1>
    </div>
    <!-- FILTROS DE LAS BITACORAS -->

    <div
      class="
        md:sticky md:top-0
        z-50
        w-full
        bg-white
        px-2
        md:p-4
        flex flex-col
        md:flex-row md:items-end
        items-center
        justify-center
        md:justify-center
        flex-wrap
        mb-4
        md:mb-0
      "
    >
      <!-- Destino -->
      <div class="md:mr-4 mx-0 w-full md:w-auto mb-2 md:mb-0">
        <h1 class="font-bold text-xxs uppercase text-left">Destino</h1>
        <select
          class="
            bg-gris-30
            rounded-md
            px-3
            focus:outline-none
            w-full
            md:w-28
            h-7
            text-xs
            md:h-8 md:text-base
          "
          v-model="filtros.destino"
          @change="aplicarFiltro()"
        >
          <option value="TODOS">TODOS</option>
          <option v-for="(x, y) in arrayHistorico.destinos" :key="y" :value="x">
            {{ x }}
          </option>
        </select>
      </div>

      <!-- BITCORA -->
      <div class="md:mr-4 mx-0 w-full md:w-auto mb-2 md:mb-0">
        <h1 class="font-bold text-xxs uppercase text-left">Bitcora</h1>
        <select
          class="
            bg-gris-30
            rounded-md
            px-3
            focus:outline-none
            w-full
            md:w-44
            h-7
            text-xs
            md:h-8 md:text-base
          "
          v-model="filtros.bitacora"
          @change="aplicarFiltro()"
        >
          <option value="TODOS">TODOS</option>
          <option
            v-for="(x, y) in arrayHistorico.bitacoras"
            :key="y"
            :value="x.toUpperCase()"
          >
            {{ x.toUpperCase() }}
          </option>
        </select>
      </div>

      <!-- PARMETRO -->
      <div class="md:mr-4 mx-0 w-full md:w-auto mb-2 md:mb-0">
        <h1 class="font-bold text-xxs uppercase text-left">Parmetro</h1>
        <select
          class="
            bg-gris-30
            rounded-md
            px-3
            focus:outline-none
            w-full
            md:w-44
            h-7
            text-xs
            md:h-8 md:text-base
          "
          v-model="filtros.parametro"
          @change="aplicarFiltro()"
        >
          <option value="TODOS">TODOS</option>
          <option
            v-for="(x, y) in arrayHistorico.parametros"
            :key="y"
            :value="x.toUpperCase()"
          >
            {{ x.toUpperCase() }}
          </option>
        </select>
      </div>

      <!-- TIPO ACTIVO -->
      <div class="md:mr-4 mx-0 w-full md:w-auto mb-2 md:mb-0">
        <h1 class="font-bold text-xxs uppercase text-left">Tipo de activo:</h1>
        <select
          class="
            bg-gris-30
            rounded-md
            px-3
            focus:outline-none
            w-full
            md:w-44
            h-7
            text-xs
            md:h-8 md:text-base
          "
          v-model="filtros.tipoEquipo"
          @change="aplicarFiltro()"
        >
          <option value="TODOS">TODOS</option>
          <option
            v-for="(x, y) in arrayHistorico.tiposEquipos"
            :key="y"
            :value="x.toUpperCase()"
          >
            {{ x.toUpperCase() }}
          </option>
        </select>
      </div>

      <!-- ACTIVO -->
      <div class="md:mr-4 mx-0 w-full md:w-auto mb-2 md:mb-0">
        <h1 class="font-bold text-xxs uppercase text-left">Activos:</h1>
        <select
          class="
            bg-gris-30
            rounded-md
            px-3
            focus:outline-none
            w-full
            md:w-44
            h-7
            text-xs
            md:h-8 md:text-base
          "
          v-model="filtros.equipo"
          @change="aplicarFiltro()"
        >
          <option value="TODOS">TODOS</option>
          <option
            v-for="(x, y) in arrayHistorico.equipos"
            :key="y"
            :value="x.toUpperCase()"
          >
            {{ x.toUpperCase() }}
          </option>
        </select>
      </div>

      <!-- DIV FECHAS -->
      <div class="flex flex-row md:mr-4 mx-0 w-full md:w-auto mb-3 md:mb-0">
        <!-- DE LA FECHA -->
        <div class="mr-3 md:mr-4 md:mx-0 w-1/2 md:w-auto md:mb-0">
          <h1 class="font-bold text-xxs uppercase text-left">De la fecha:</h1>
          <input
            type="date"
            class="
              bg-gris-30
              rounded-md
              px-3
              focus:outline-none
              w-full
              md:w-auto
              h-7
              text-xs
              md:h-8 md:text-base
            "
            v-model="filtros.fechaInicio"
            @change="obtenerArrayHistorico()"
          />
        </div>

        <!-- A LA FECHA -->
        <div class="ml-3 md:mr-4 md:mx-0 w-1/2 md:w-auto md:mb-0">
          <h1 class="font-bold text-xxs uppercase text-left">A la fecha:</h1>
          <input
            type="date"
            class="
              bg-gris-30
              rounded-md
              px-3
              focus:outline-none
              w-full
              md:w-auto
              h-7
              text-xs
              md:h-8 md:text-base
            "
            v-model="filtros.fechaFin"
            @change="obtenerArrayHistorico()"
          />
        </div>
      </div>

      <!-- SEARCH -->
      <div class="flex flex-row">
        <div
          class="
            w-full
            relative
            flex
            items-center
            md:w-auto
            mb-4
            md:mb-0
            mr-0
            md:mr-4
            bg-gris-30
            rounded-lg
          "
        >
          <input
            type="seach"
            class="
              bg-gris-30
              w-full
              md:w-auto
              pl-6
              h-8
              rounded-lg
              focus:outline-none
            "
            placeholder="Buscar..."
            v-model="filtros.palabra"
            @keyup="aplicarFiltro()"
          />

          <button
            class="
              focus:outline-none
              bg-bluedemon-200
              rounded-r-md
              h-8
              w-8
              flex
              items-center
              justify-center
              bg-gris-30
            "
          >
            <svg
              xmlns="http://www.w3.org/2000/svg"
              class="w-4 h-4 text-bluedemon-400"
              viewBox="0 0 24 24"
              fill="none"
              stroke="currentColor"
              stroke-width="3"
              stroke-linecap="round"
              stroke-linejoin="round"
            >
              <circle cx="11" cy="11" r="8"></circle>
              <line x1="21" y1="21" x2="16.65" y2="16.65"></line>
            </svg>
          </button>
        </div>

        <!-- BOTN APLICAR -->
        <div class="w-10/12 md:w-auto mx-0 hidden">
          <button
            class="
              bg-gray-200
              text-gray-500
              h-8
              rounded-lg
              px-2
              text-xs
              w-10/12
              md:w-auto
              fw-500
              uppercase
            "
            @click="obtenerArrayHistorico()"
          >
            Aplicar
          </button>
        </div>
      </div>
    </div>

    <button
      class="
        bg-indigo-200
        text-indigo-500
        rounded-lg
        p-2
        fw-500
        w-40
        mb-4
        font-bold
      "
      @click="$router.push({ path: '/graficos' })"
    >
      Graficos
    </button>

    <!-- TABLA -->
    <div
      class="
        max-w-full
        md:max-w-9xl
        bg-white
        shadow-md
        rounded
        font-sans
        overflow-y-scroll overflow-x-scroll
        md:overflow-y-scroll md:overflow-hidden
        table-responsive
      "
    >
      <table class="">
        <thead>
          <tr class="bg-gray-200 text-gray-600 uppercase leading-normal">
            <th>Equipo</th>
            <th>Bitcora</th>
            <th>Parmetro</th>
            <th>UDM</th>
            <th>Fecha Solicitada</th>
            <th>Fecha Registro</th>
            <th>Registro</th>
            <th>FDR</th>
            <th>Incidencia?</th>
            <th>Capturado Por</th>
          </tr>
        </thead>

        <tbody
          class="
            text-gray-600 text-center text-xs
            md:text-sm
            whitespace-nowrap
            font-normal
          "
        >
          <tr
            v-for="(x, y) in arrayHistorico.data"
            :key="y"
            class="border-b border-gray-200 hover:bg-gray-100"
          >
            <!-- Equipo -->
            <td class="py-3 px-4 border-r border-l text-left">
              {{ x.equipo }}
            </td>

            <!-- Bitcora -->
            <td class="py-3 px-4 text-left">
              {{ x.bitacora }}
            </td>

            <!-- Parmetro -->
            <td class="py-3 px-4 text-left border-r border-l">
              {{ x.parametro }}
            </td>

            <!-- UDM -->
            <td class="py-3 px-4 border-r border-l">
              {{ x.medida }}
            </td>

            <!-- Fecha Solicitada-->
            <td class="px-4 border-r border-l">
              <p class="text-xs">
                {{ x.fechaSolicitada }}
              </p>

              <p>
                {{ x.horaSolicitada }}
              </p>
            </td>

            <!-- Fecha Registro -->
            <td class="px-4 border-r border-l">
              <p class="text-xs">
                {{ x.fechaRegistro }}
              </p>

              <p>
                {{ x.horaRegistro }}
              </p>
            </td>

            <!-- Registro -->
            <td class="w-full h-full flex justify-center items-center">
              <input
                type="number"
                class="
                  w-20
                  py-2
                  focus:outline-none
                  bg-white
                  hover:bg-gray-100
                  font-bold
                  text-red-500
                  focus:bg-white
                  text-center
                "
                v-model="x.valor"
                @change="capturar(x)"
              />
            </td>

            <!-- FDR -->
            <td class="py-3 px-4 border-r border-l uppercase">
              {{ x.fueraRango }}
            </td>

            <!-- Incidencia? -->
            <td class="py-3 px-4 border-r border-l uppercase">
              {{ x.crearIncidencia }}
            </td>

            <!-- Capturado Por -->
            <td class="py-3 px-4 border-r border-l uppercase">
              {{ x.nombre }}
            </td>
          </tr>
        </tbody>
      </table>
    </div>

    <!-- TABLA -->
  </div>
</template>

<script>
import { computed, onMounted, ref } from "@vue/runtime-core";
import { useStore } from "vuex";

export default {
  name: "Bitacorashistorico",
  props: {},

  setup() {
    const store = useStore();
    const Swal = require("sweetalert2");
    const Toast = Swal.mixin({
      toast: true,
      padding: ".3rem",
      background: "#2c3e50",
      iconColor: "#fff",
      position: "top",
      customClass: {
        popup: "colored-toast",
      },
      width: "19rem",
      showConfirmButton: false,
      timer: 2700,
      timerProgressBar: true,
      didOpen: (toast) => {
        toast.addEventListener("mouseenter", Swal.stopTimer);
        toast.addEventListener("mouseleave", Swal.resumeTimer);
      },
    });

    const valores = {
      valor: 5,
      valorMinimo: 1,
      valorMaximo: 10,
    };
    const date = new Date();
    const mes = date.getMonth() + 1;
    const dia = date.getDate();
    const month = mes < 10 ? "0" + mes : mes;
    const day = dia < 10 ? "0" + dia : dia;
    const hoy = date.getFullYear() + "-" + month + "-" + day;

    const filtros = ref({
      destino: "TODOS",
      bitacora: "TODOS",
      parametro: "TODOS",
      tipoEquipo: "TODOS",
      equipo: "TODOS",
      fechaInicio: hoy,
      fechaFin: hoy,
      palabra: "",
    });

    onMounted(async () => {
      await obtenerArrayHistorico();
    });

    const arrayHistorico = computed(() => {
      return store.state.arrayHistorico;
    });

    const obtenerArrayHistorico = async () => {
      try {
        const response = await store.dispatch("getConexion", {
          idDestino: localStorage.getItem("idDestino"),
          idUsuario: localStorage.getItem("usuario"),
          action: "conexion",
        });

        if (response) {
          store.dispatch("getArrayHistorico", {
            idDestino: localStorage.getItem("idDestino"),
            idUsuario: localStorage.getItem("usuario"),
            fechaInicio: filtros.value.fechaInicio,
            fechaFin: filtros.value.fechaFin,
            action: "historico",
          });
          aplicarFiltro();
        }
      } catch (error) {
        console.log(error);
      }
    };

    const aplicarFiltro = () => {
      const array = store.state.arrayHistoricoFull;

      const arrayNuevo = array.filter((x) => {
        if (
          (x.destino.toUpperCase() == filtros.value.destino.toUpperCase() ||
            filtros.value.destino.toUpperCase() == "TODOS") &&
          (x.bitacora.toUpperCase() == filtros.value.bitacora.toUpperCase() ||
            filtros.value.bitacora.toUpperCase() == "TODOS") &&
          (x.parametro.toUpperCase() == filtros.value.parametro.toUpperCase() ||
            filtros.value.parametro.toUpperCase() == "TODOS") &&
          (x.tipoEquipo.toUpperCase() ==
            filtros.value.tipoEquipo.toUpperCase() ||
            filtros.value.tipoEquipo.toUpperCase() == "TODOS") &&
          (x.equipo.toUpperCase() == filtros.value.equipo.toUpperCase() ||
            filtros.value.equipo.toUpperCase() == "TODOS") &&
          (x.equipo.toUpperCase().indexOf(filtros.value.palabra.toUpperCase()) >
            -1 ||
            x.destino
              .toUpperCase()
              .indexOf(filtros.value.palabra.toUpperCase()) > -1 ||
            x.bitacora
              .toUpperCase()
              .indexOf(filtros.value.palabra.toUpperCase()) > -1 ||
            x.parametro
              .toUpperCase()
              .indexOf(filtros.value.palabra.toUpperCase()) > -1 ||
            x.medida
              .toUpperCase()
              .indexOf(filtros.value.palabra.toUpperCase()) > -1 ||
            x.horarioInput
              .toUpperCase()
              .indexOf(filtros.value.palabra.toUpperCase()) > -1 ||
            x.fechaRegistro
              .toUpperCase()
              .indexOf(filtros.value.palabra.toUpperCase()) > -1 ||
            x.horaRegistro
              .toUpperCase()
              .indexOf(filtros.value.palabra.toUpperCase()) > -1 ||
            x.valor.indexOf(filtros.value.palabra) > -1 ||
            x.enTiempo
              .toUpperCase()
              .indexOf(filtros.value.palabra.toUpperCase()) > -1 ||
            x.crearIncidencia
              .toUpperCase()
              .indexOf(filtros.value.palabra.toUpperCase()) > -1 ||
            filtros.value.palabra == "")
        ) {
          return true;
        }
      });
      store.state.arrayHistorico.data = arrayNuevo;
    };

    const capturar = (equipo) => {
      try {
        const hoy = new Date();

        const data = {
          idDestino: localStorage.getItem("idDestino"),
          idUsuario: localStorage.getItem("usuario"),
          action: "capturar",
          idBitacora: equipo.idBitacora,
          idParametro: equipo.idParametro,
          idLineaTiempo: equipo.idLineaTiempo,
          idEquipo: equipo.idEquipo,
          idValor: equipo.idValor,
          equipo: equipo.equipo,
          medida: equipo.medida,
          parametroMinimo: equipo.parametroMinimo,
          parametroMaximo: equipo.parametroMaximo,
          valor: equipo.valor,
          crearIncidencia: equipo.crearIncidencia,
          idIncidencia: equipo.idIncidencia,
          fechaCaptura: equipo.fechaCaptura,
          tipoEquipo: equipo.tipoEquipo,
          horarioInput: equipo.horarioInput,
          fechaToken: equipo.fechaToken,
          status: equipo.status,
          enTiempo: equipo.enTiempo,
          fechaCapturaActual: `${hoy.getFullYear()}-${
            hoy.getMonth() + 1
          }-${hoy.getDate()} ${hoy.getHours()}:${hoy.getMinutes()}:${hoy.getSeconds()}`,
        };

        Swal.fire({
          title: "Desea Cambiar el Valor?",
          icon: "question",
          background: "#2c3e50",
          customClass: {
            popup: "colored-toast",
            backdrop: "swal2-backdrop-show",
            icon: "swal2-icon-show",
          },
          showDenyButton: true,
          showCancelButton: true,
          confirmButtonText: `Si, Cambiar`,
          denyButtonText: `No, Descartar`,
        }).then(async (result) => {
          if (result.isConfirmed) {
            const response = await store.dispatch("getCaptura", data);

            if (response) {
              store.state.conexion = true;
              obtenerArrayHistorico();
              Toast.fire({
                icon: "success",
                title: "Captura Completada!",
              });
            }

            if (!response) {
              store.state.conexion = false;

              Toast.fire({
                icon: "info",
                title: "Intente de Nuevo!",
              });
            }
          } else if (result.isDenied) {
            Toast.fire({
              icon: "info",
              title: "Cambio Descartado!",
            });
          }
        });
      } catch (error) {
        store.state.conexion = false;
      }
    };

    return {
      arrayHistorico,
      filtros,
      obtenerArrayHistorico,
      aplicarFiltro,
      capturar,
      valores,
    };
  },
};
</script>

<style scoped>
.table-responsive {
  height: 48rem;
}

@media only screen and (max-width: 600px) {
  .table-responsive {
    height: 25rem;
  }
}

::-webkit-scrollbar {
  width: 6px;
  height: 6px;
}
/* Track */
::-webkit-scrollbar-track {
  -webkit-border-radius: 6px;
  border-radius: 6px;
}

/* Handle */
::-webkit-scrollbar-thumb {
  -webkit-border-radius: 6px;
  border-radius: 6px;
  background: rgba(0, 0, 0, 0.5);
  -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.5);
}

::-webkit-scrollbar-thumb:window-inactive {
  background: rgba(238, 251, 252, 0.822);
}

th {
  position: sticky;
  top: 0;
  background-color: #e5e7eb;

  @apply py-1 px-2 md:py-3 text-center text-xs md:text-sm;
}
</style>