<template>
  <div class="flex flex-col items-center justify-start">
    <div class="p-4 leading-none">
      <h1 class="fw-700 text-xl">Grficos Bitcoras</h1>
    </div>

    <!-- FILTROS DE LAS BITACORAS -->
    <div
      class="
        md:sticky md:top-0
        z-50
        w-full
        bg-white
        p-4
        flex flex-col
        md:flex-row md:items-end
        items-center
        justify-center
        md:justify-center
        flex-wrap
        mb-4
        md:mb-0
      "
    >
      <!-- Destino -->
      <div class="md:mr-4 mx-0 w-full md:w-auto mb-3 md:mb-0">
        <h1 class="font-bold text-xxs uppercase text-left">Destino</h1>
        <select
          class="
            bg-gris-30
            rounded-md
            px-3
            h-8
            focus:outline-none
            w-full
            md:w-auto
          "
          v-model="filtros.destino"
          @change="aplicarFiltro()"
        >
          <option value="TODOS">TODOS</option>
          <option
            v-for="(item, index) in arrayDestinos"
            :key="index"
            :value="item"
          >
            {{ item }}
          </option>
        </select>
      </div>

      <!-- Bitcora -->
      <div class="md:mr-4 mx-0 w-full md:w-auto mb-3 md:mb-0">
        <h1 class="font-bold text-xxs uppercase text-left">Bitcora</h1>
        <select
          class="
            bg-gris-30
            rounded-md
            px-3
            h-8
            focus:outline-none
            w-full
            md:max-w-xxs
          "
          v-model="filtros.bitacora"
          @change="aplicarFiltro()"
        >
          <option value="TODOS">TODOS</option>
          <option
            v-for="(item, index) in arrayBitacoras"
            :key="index"
            :value="item"
          >
            {{ item }}
          </option>
        </select>
      </div>

      <!-- Parmetro -->
      <div class="md:mr-4 mx-0 w-full md:w-auto mb-3 md:mb-0">
        <h1 class="font-bold text-xxs uppercase text-left">Parmetro</h1>
        <select
          class="
            bg-gris-30
            rounded-md
            px-3
            h-8
            focus:outline-none
            w-full
            md:max-w-xxs
          "
          v-model="filtros.parametro"
          @change="aplicarFiltro()"
        >
          <option value="TODOS">TODOS</option>
          <option
            v-for="(item, index) in arrayParametros"
            :key="index"
            :value="item"
          >
            {{ item }}
          </option>
        </select>
      </div>

      <!-- Tipo Activo -->
      <div class="md:mr-4 mx-0 w-full md:w-auto mb-3 md:mb-0">
        <h1 class="font-bold text-xxs uppercase text-left">Tipo de activo:</h1>
        <select
          class="
            bg-gris-30
            rounded-md
            px-3
            h-8
            focus:outline-none
            w-full
            md:max-w-xxs
          "
          v-model="filtros.tipoEquipo"
          @change="aplicarFiltro()"
        >
          <option value="TODOS">TODOS</option>
          <option
            v-for="(item, index) in arrayTiposActivos"
            :key="index"
            :value="item"
          >
            {{ item }}
          </option>
        </select>
      </div>

      <!-- Activos -->
      <div class="md:mr-4 mx-0 w-full md:w-auto mb-3 md:mb-0">
        <h1 class="font-bold text-xxs uppercase text-left">Activos:</h1>
        <select
          class="
            bg-gris-30
            rounded-md
            px-3
            h-8
            focus:outline-none
            w-full
            md:w-auto
          "
          v-model="filtros.equipo"
          @change="aplicarFiltro()"
        >
          <option value="TODOS">TODOS</option>
          <option
            v-for="(item, index) in arrayActivos"
            :key="index"
            :value="item"
          >
            {{ item }}
          </option>
        </select>
      </div>

      <!-- De la Fecha -->
      <div class="md:mr-4 mx-0 w-full md:w-auto mb-3 md:mb-0">
        <h1 class="font-bold text-xxs uppercase text-left">De la fecha:</h1>
        <input
          type="date"
          name=""
          id=""
          class="
            bg-gris-30
            rounded-md
            px-3
            h-8
            focus:outline-none
            w-full
            md:w-auto
          "
          v-model="filtros.fechaInicio"
          @change="arrayGraficos()"
        />
      </div>

      <!-- a la Fecha -->
      <div class="md:mr-4 mx-0 w-full md:w-auto mb-3 md:mb-0">
        <h1 class="font-bold text-xxs uppercase text-left">A la fecha:</h1>
        <input
          type="date"
          name=""
          id=""
          class="
            bg-gris-30
            rounded-md
            px-3
            h-8
            focus:outline-none
            w-full
            md:w-auto
          "
          v-model="filtros.fechaFin"
          @change="arrayGraficos()"
        />
      </div>

      <!-- Search -->
      <div
        class="
          relative
          flex
          items-center
          w-full
          md:w-auto
          px-10
          md:px-0
          mb-2
          md:mb-0
          mr-0
          md:mr-4
        "
      >
        <input
          type="seach"
          class="
            bg-gris-30
            w-full
            md:w-auto
            rounded-l-md
            pl-6
            h-8
            focus:outline-none
          "
          placeholder="Buscar..."
          v-model="filtros.palabra"
          @keyup="aplicarFiltro()"
        />
        <svg
          xmlns="http://www.w3.org/2000/svg"
          class="w-4 h-4 absolute top-0 mt-2 ml-1 text-gris-600"
          viewBox="0 0 24 24"
          fill="none"
          stroke="currentColor"
          stroke-width="3"
          stroke-linecap="round"
          stroke-linejoin="round"
        >
          <circle cx="11" cy="11" r="8"></circle>
          <line x1="21" y1="21" x2="16.65" y2="16.65"></line>
        </svg>
        <button
          class="
            focus:outline-none
            bg-bluedemon-200
            rounded-r-md
            h-8
            w-8
            flex
            items-center
            justify-center
          "
        >
          <svg
            xmlns="http://www.w3.org/2000/svg"
            class="w-4 h-4 text-bluedemon-400"
            viewBox="0 0 24 24"
            fill="none"
            stroke="currentColor"
            stroke-width="3"
            stroke-linecap="round"
            stroke-linejoin="round"
          >
            <circle cx="11" cy="11" r="8"></circle>
            <line x1="21" y1="21" x2="16.65" y2="16.65"></line>
          </svg>
        </button>
      </div>

      <!-- Aplicar -->
      <div class="mx-0 w-10/12 md:w-auto hidden">
        <button
          class="
            bg-gray-200
            text-gray-500
            h-8
            rounded-lg
            px-2
            text-xs
            w-10/12
            md:w-auto
            fw-500
            uppercase
          "
          @click="arrayGraficos()"
        >
          Aplicar
        </button>
      </div>
    </div>

    <!-- BOTONTES -->
    <div class="flex flex-row justify-center items-center">
      <!-- Toggle B -->
      <button
        @click="vista = !vista"
        class="rounded-lg p-2 font-bold m-2"
        :class="{
          'bg-green-200 text-green-500': vista,
          'bg-blue-200 text-blue-500': !vista,
        }"
      >
        Graficos
        <span v-if="vista">Lineas</span>
        <span v-if="!vista">Gauge</span>
      </button>

      <button
        class="
          bg-yellow-200
          text-yellow-500
          rounded-lg
          p-2
          fw-500
          m-2
          font-bold
        "
        @click="$router.push({ path: '/historico' })"
      >
        Tabla de Histricos
      </button>
    </div>

    <!-- Grafico Gauge -->
    <div
      v-if="vista"
      class="px-3 w-full flex flex-wrap justify-center p-1 sm:p-4"
    >
      <div
        v-for="(item, index) in array"
        :key="index"
        class="
          w-full
          md:max-w-max
          flex flex-col
          justify-between
          items-center
          m-1.5
          shadow-sm
          rounded-lg
        "
        style="background-color: #f8fafa"
      >
        <!-- Equipo -->
        <h2 class="estilo-gauge text-xs py-1.5">{{ item.equipo }}</h2>

        <!-- Parametro -->
        <h3 class="estilo-gauge text-xxs">{{ item.parametro }}</h3>

        <vue-gauge
          :refid="'type-unique-id-' + index"
          :options="{
            needleValue:
              (100 /
                (item.data[item.data.length - 1].parametroMaximo -
                  item.data[item.data.length - 1].parametroMinimo)) *
                (item.data[item.data.length - 1].valor -
                  item.data[item.data.length - 1].parametroMinimo) >
              80
                ? (100 /
                    (item.data[item.data.length - 1].parametroMaximo -
                      item.data[item.data.length - 1].parametroMinimo)) *
                    (item.data[item.data.length - 1].valor -
                      item.data[item.data.length - 1].parametroMinimo) -
                  10
                : (100 /
                    (item.data[item.data.length - 1].parametroMaximo -
                      item.data[item.data.length - 1].parametroMinimo)) *
                    (item.data[item.data.length - 1].valor -
                      item.data[item.data.length - 1].parametroMinimo) +
                  10,
            arcDelimiters: [10, 90],
            hasNeedle: true,
            needleColor: '#022f34',
            arcColors: ['#f9b714', '#2ccda2', '#f9b714'],
            arcLabels: [
              item.data[item.data.length - 1].parametroMinimo,
              item.data[item.data.length - 1].parametroMaximo,
            ],
            rangeLabel: ['Min', 'Max'],
            chartWidth: 275,
          }"
          class="h-28 mb-4"
        />
        {{ item.data[item.data.length - 1].valor }}
        {{ item.data[item.data.length - 1].medida }}
      </div>
    </div>

    <!-- Grafico Lineal -->
    <div
      v-if="!vista"
      class="w-full flex flex-wrap items-center justify-center p-1 sm:p-4"
    >
      <Grafico
        v-for="(x, y) in array"
        :key="y"
        class="
          w-full
          sm:w-1/3
          xl:w-1/3
          2xl:w-1/4
          h-56
          truncate
          px-1
          sm:px-4
          text-center
        "
        :array="x"
      />
    </div>
  </div>
</template>

<script>
import Grafico from "@/components/Grafico.vue";
import { ref } from "@vue/reactivity";
import { useStore } from "vuex";
import addDate from "date-fns/add";
import lightFormat from "date-fns/lightFormat";
import VueGauge from "vue-gauge";
import { onMounted } from "@vue/runtime-core";

export default {
  components: { Grafico, VueGauge },
  props: {},

  setup() {
    const store = useStore();

    onMounted(() => {
      arrayGraficos();
    });

    const vista = ref(true);
    const hoy = lightFormat(addDate(new Date(), { days: 0 }), "yyyy-MM-dd");
    const ayer = lightFormat(addDate(new Date(), { days: -1 }), "yyyy-MM-dd");
    const array = ref([]);
    const arrayDestinos = ref([]);
    const arrayBitacoras = ref([]);
    const arrayParametros = ref([]);
    const arrayTiposActivos = ref([]);
    const arrayActivos = ref([]);

    const filtros = ref({
      destino: "TODOS",
      bitacora: "TODOS",
      parametro: "TODOS",
      tipoEquipo: "TODOS",
      equipo: "TODOS",
      fechaInicio: ayer,
      fechaFin: hoy,
      palabra: "",
    });

    const x = async () => {
      store.state.arrayGraficos = [];

      const response = await store.dispatch("accionesUsuario", {
        idDestino: localStorage.getItem("idDestino"),
        idUsuario: localStorage.getItem("usuario"),
        fechaInicio: filtros.value.fechaInicio,
        fechaFin: filtros.value.fechaFin,
        action: "graficos",
      });

      if (response.response) {
        const data = response.data;
        // array.value = data.graficos;
        store.state.arrayGraficos = data.graficos;

        // Destinos
        data.destinos.forEach((item) => {
          if (!arrayDestinos.value.includes(item.toUpperCase()))
            arrayDestinos.value.push(item.toUpperCase());
        });

        // Bitacoras
        data.bitacoras.forEach((item) => {
          if (!arrayBitacoras.value.includes(item.toUpperCase()))
            arrayBitacoras.value.push(item.toUpperCase());
        });

        // Parametros
        data.parametros.forEach((item) => {
          if (!arrayParametros.value.includes(item.toUpperCase()))
            arrayParametros.value.push(item.toUpperCase());
        });

        // Tipos Activos
        data.tiposEquipos.forEach((item) => {
          if (!arrayTiposActivos.value.includes(item.toUpperCase()))
            arrayTiposActivos.value.push(item.toUpperCase());
        });

        // Activos
        data.equipos.forEach((item) => {
          if (!arrayActivos.value.includes(item.toUpperCase()))
            arrayActivos.value.push(item.toUpperCase());
        });

        return;
      }
    };

    const arrayGraficos = async () => {
      store.state.arrayGraficos = [];
      array.value = [];
      arrayDestinos.value = [];
      arrayBitacoras.value = [];
      arrayParametros.value = [];
      arrayTiposActivos.value = [];
      arrayActivos.value = [];

      const response = await store.dispatch("accionesUsuario", {
        idDestino: localStorage.getItem("idDestino"),
        idUsuario: localStorage.getItem("usuario"),
        fechaInicio: filtros.value.fechaInicio,
        fechaFin: filtros.value.fechaFin,
        action: "graficaX",
      });

      if (response.response) {
        const arrayX = [];
        const data = response.data.data;

        // Destinos
        data.destinos.forEach((item) => {
          if (!arrayDestinos.value.includes(item.toUpperCase()))
            arrayDestinos.value.push(item.toUpperCase());
        });

        // Bitacoras
        data.bitacoras.forEach((item) => {
          if (!arrayBitacoras.value.includes(item.toUpperCase()))
            arrayBitacoras.value.push(item.toUpperCase());
        });

        // Parametros
        data.parametros.forEach((item) => {
          if (!arrayParametros.value.includes(item.toUpperCase()))
            arrayParametros.value.push(item.toUpperCase());
        });

        // Tipos Activos
        data.tiposEquipos.forEach((item) => {
          if (!arrayTiposActivos.value.includes(item.toUpperCase()))
            arrayTiposActivos.value.push(item.toUpperCase());
        });

        // Activos
        data.equipos.forEach((item) => {
          if (!arrayActivos.value.includes(item.toUpperCase()))
            arrayActivos.value.push(item.toUpperCase());
        });

        data.graficas.forEach((item, index) => {
          const estructura = {
            idUnico: item.idEquipo + item.idBitacora + item.idParametro,
            idEquipo: item.idEquipo,
            equipo: item.equipo,
            tipoEquipo: item.tipoEquipo,
            destino: item.destino,
            idBitacora: item.idBitacora,
            bitacora: item.bitacora,
            idParametro: item.idParametro,
            parametro: item.parametro,
            data: [],
          };

          const datos = {
            fechaTokenFull: item.fechaTokenFull,
            fechaTokenCorta: item.fechaTokenCorta,
            valor: item.valor,
            parametroMinimo: item.parametroMinimo,
            parametroMaximo: item.parametroMaximo,
            medida: item.medida,
          };

          if (
            !arrayX.some(
              (item2) =>
                item2.idEquipo == item.idEquipo &&
                item2.idBitacora == item.idBitacora &&
                item2.idParametro == item.idParametro
            )
          ) {
            estructura.data.push(datos);
            arrayX.push(estructura);
            return;
          }

          arrayX.forEach((item2, index2) => {
            if (
              item2.idEquipo == item.idEquipo &&
              item2.idBitacora == item.idBitacora &&
              item2.idParametro == item.idParametro
            ) {
              arrayX[index2].data.push(datos);
            }
          });
        });

        array.value = arrayX;
        store.state.arrayGraficos = arrayX;
        aplicarFiltro();
      }
    };

    const aplicarFiltro = () => {
      const arrayNuevo = store.state.arrayGraficos.filter((x) => {
        if (
          (x.destino.toUpperCase() === filtros.value.destino.toUpperCase() ||
            filtros.value.destino.toUpperCase() === "TODOS") &&
          (x.bitacora.toUpperCase() === filtros.value.bitacora.toUpperCase() ||
            filtros.value.bitacora.toUpperCase() === "TODOS") &&
          (x.parametro.toUpperCase() ===
            filtros.value.parametro.toUpperCase() ||
            filtros.value.parametro.toUpperCase() === "TODOS") &&
          (x.tipoEquipo.toUpperCase() ===
            filtros.value.tipoEquipo.toUpperCase() ||
            filtros.value.tipoEquipo.toUpperCase() === "TODOS") &&
          (x.equipo.toUpperCase() === filtros.value.equipo.toUpperCase() ||
            filtros.value.equipo.toUpperCase() === "TODOS") &&
          (x.equipo.toUpperCase().indexOf(filtros.value.palabra.toUpperCase()) >
            -1 ||
            x.destino
              .toUpperCase()
              .indexOf(filtros.value.palabra.toUpperCase()) > -1 ||
            x.bitacora
              .toUpperCase()
              .indexOf(filtros.value.palabra.toUpperCase()) > -1 ||
            x.parametro
              .toUpperCase()
              .indexOf(filtros.value.palabra.toUpperCase()) > -1 ||
            filtros.value.palabra === "")
        ) {
          return true;
        }
      });
      array.value = arrayNuevo;
    };

    return {
      filtros,
      arrayGraficos,
      vista,
      array,
      arrayDestinos,
      arrayBitacoras,
      arrayParametros,
      arrayTiposActivos,
      arrayActivos,
      aplicarFiltro,
    };
  },
};
</script>

<style scoped>
.padre {
  display: grid;
  grid-template-columns: repeat(365, 35px);
  grid-template-rows: auto;
  grid-column-gap: 0px;
  grid-row-gap: 0px;
  font-size: 10px;
}
.hijo {
  grid-row-start: 2;
  grid-column-start: 1;
  grid-column-end: 6;
  mix-blend-mode: multiply;
  border-left-width: 3px;
  border-left-color: gold;
  border-top-right-radius: 3px;
  border-bottom-right-radius: 3px;
}
.hijo2 {
  border-left-width: 3px;
  border-left-color: greenyellow;
  border-top-right-radius: 3px;
  border-bottom-right-radius: 3px;
  grid-row-start: 2;
  grid-column-start: 5;
  grid-column-end: 10;
  mix-blend-mode: multiply;
}
.hijo3 {
  border-left-width: 3px;
  border-left-color: dodgerblue;
  border-top-right-radius: 3px;
  border-bottom-right-radius: 3px;
  grid-row-start: 3;
  grid-column-start: 40;
  grid-column-end: 53;
  mix-blend-mode: multiply;
}

.hijo:hover {
  height: 30px;
}
.hijo2:hover {
  zoom: 200%;
}
.hijo3:hover {
  zoom: 200%;
}

.estilo-gauge {
  @apply truncate text-center font-bold text-gray-500;
}
</style>
