<template>
  <!-- HOY -->
  <div
    @click="abierto = !abierto"
    class="
      cursor-pointer
      p-2
      text-left
      fw-600
      mb-2
      flex flex-row
      items-center
      justify-between
    "
    v-if="tipo == 'HOY' && array.parametros.length && visible"
  >
    <h1>
      <span>{{ indexPadre + 1 }} .- </span>{{ array.equipo }}
    </h1>

    <svg
      :class="{ girar: abierto }"
      xmlns="http://www.w3.org/2000/svg"
      width="24"
      height="24"
      viewBox="0 0 24 24"
      fill="none"
      stroke="currentColor"
      stroke-width="2"
      stroke-linecap="round"
      stroke-linejoin="round"
    >
      <path d="M6 9l6 6 6-6" />
    </svg>
  </div>

  <div v-if="abierto && tipo == 'HOY' && visible" class="bg-gris-30 px-2 py-4">
    <Valores
      v-for="(x, y) in array.parametros"
      :key="y"
      :array="x"
      :indexPadre="indexPadre"
      :indexHijo="y"
      :tipo="tipo"
    />
  </div>

  <!-- PENDIENTES -->
  <div
    @click="abierto = !abierto"
    class="
      cursor-pointer
      p-2
      text-left
      fw-600
      mb-2
      flex flex-row
      items-center
      justify-between
    "
    v-if="tipo == 'PENDIENTES' && array.parametros.length && visible"
  >
    <h1>
      <span>{{ indexPadre + 1 }} .- </span>{{ array.equipo }}
    </h1>
    <svg
      :class="{ girar: abierto }"
      xmlns="http://www.w3.org/2000/svg"
      width="24"
      height="24"
      viewBox="0 0 24 24"
      fill="none"
      stroke="currentColor"
      stroke-width="2"
      stroke-linecap="round"
      stroke-linejoin="round"
    >
      <path d="M6 9l6 6 6-6" />
    </svg>
  </div>

  <div
    v-if="abierto && tipo == 'PENDIENTES' && array.parametros && visible"
    class="bg-gris-30 px-2 py-4"
  >
    <Valores
      v-for="(x, y) in array.parametros"
      :key="y"
      :array="x"
      :indexPadre="indexPadre"
      :indexHijo="y"
      :tipo="tipo"
    />
  </div>

  <!-- LLENOS -->
  <div
    @click="abierto = !abierto"
    class="
      cursor-pointer
      p-2
      text-left
      fw-600
      mb-2
      flex flex-row
      items-center
      justify-between
    "
    v-if="tipo == 'LLENOS' && array.parametros.length && visible"
  >
    <h1>
      <span>{{ indexPadre + 1 }} .- </span>{{ array.equipo }}
    </h1>
    <svg
      :class="{ girar: abierto }"
      xmlns="http://www.w3.org/2000/svg"
      width="24"
      height="24"
      viewBox="0 0 24 24"
      fill="none"
      stroke="currentColor"
      stroke-width="2"
      stroke-linecap="round"
      stroke-linejoin="round"
    >
      <path d="M6 9l6 6 6-6" />
    </svg>
  </div>

  <div
    v-if="abierto && tipo == 'LLENOS' && array.parametros.length && visible"
    class="bg-gris-30 px-2 py-4"
  >
    <Valores
      v-for="(x, y) in array.parametros"
      :key="y"
      :array="x"
      :indexPadre="indexPadre"
      :indexHijo="y"
      :tipo="tipo"
    />
  </div>
</template>

<script>
import { ref } from "@vue/reactivity";
import Valores from "./Valores.vue";
export default {
  components: { Valores },
  props: {
    array: {
      type: Object,
      required: false,
    },

    indexPadre: {
      type: Number,
      required: false,
      value: 0,
    },

    tipo: {
      type: String,
    },
  },
  setup(props) {
    const abierto = ref(false);
    const visible = ref(true);
    const arrayX = props.array.parametros;
    const tipoX = props.tipo;
    let contador = 0;

    arrayX.forEach((item) => {
      if (tipoX === "PENDIENTES") {
        if (item.status === "PENDIENTE") {
          contador++;
        }
        return;
      }

      if (tipoX === "LLENOS") {
        if (item.status === "CAPTURADO") {
          contador++;
        }
        return;
      }

      if (tipoX === "HOY") {
        contador++;
        return;
      }
    });

    if (contador === 0) visible.value = false;

    return { abierto, visible };
  },
};
</script>

<style scoped>
.girar {
  transform: rotate(180deg);
}
</style>
