<script>
import { useStore } from "vuex";
export default {
  props: {
    array: {
      type: Object,
      requiere: true,
    },

    tipo: {
      type: String,
    },

    indexPadre: {
      type: Number,
      required: false,
      value: 0,
    },

    indexHijo: {
      type: Number,
      required: false,
      value: 0,
    },
  },

  setup() {
    const store = useStore();

    const Swal = require("sweetalert2");

    const Toast = Swal.mixin({
      toast: true,
      padding: ".3rem",
      background: "#2c3e50",
      iconColor: "#fff",
      position: "top",
      customClass: {
        popup: "colored-toast",
      },
      width: "19rem",
      showConfirmButton: false,
      timer: 2700,
      timerProgressBar: true,
      didOpen: (toast) => {
        toast.addEventListener("mouseenter", Swal.stopTimer);
        toast.addEventListener("mouseleave", Swal.resumeTimer);
      },
    });

    const capturar = async (array, indexPadre, indexHijo) => {
      try {
        const hoy = new Date();
        const fechaCapturaActual = `${hoy.getFullYear()}-${
          hoy.getMonth() + 1
        }-${hoy.getDate()} ${hoy.getHours()}:${hoy.getMinutes()}:${hoy.getSeconds()}`;

        const data = {
          action: "capturar",
          crearIncidencia: array.crearIncidencia,
          enTiempo: array.enTiempo,
          equipo: array.equipo,
          fechaCaptura: array.fechaCaptura,
          fechaCapturaActual: fechaCapturaActual,
          fechaToken: array.fechaToken,
          horarioInput: array.horarioInput,
          idBitacora: array.idBitacora,
          idDestino: localStorage.getItem("idDestino"),
          idEquipo: array.idEquipo,
          idIncidencia: array.idIncidencia,
          idLineaTiempo: array.idLineaTiempo,
          idParametro: array.idParametro,
          idUsuario: localStorage.getItem("usuario"),
          idValor: array.idValor,
          medida: array.medida,
          parametroMaximo: array.parametroMaximo,
          parametroMinimo: array.parametroMinimo,
          status: array.status,
          tipoEquipo: array.tipoEquipo,
          valor: array.valor,
        };

        const response = await store.dispatch("getCaptura", data);

        if (response) {
          store.state.conexion = true;
          store.dispatch("getArray", {
            idDestino: localStorage.getItem("idDestino"),
            idUsuario: localStorage.getItem("usuario"),
            action: "getArrayNueva",
          });

          Toast.fire({
            icon: "success",
            title: "Captura Completada!",
          });
        }

        if (!response) {
          store.state.conexion = false;

          // SE APILAN PENDIENTES
          if (localStorage.getItem("dataOffline")) {
            const array = JSON.parse(localStorage.getItem("dataOffline"));
            array.push(data);
            localStorage.setItem("dataOffline", JSON.stringify(array));

            Toast.fire({
              icon: "success",
              title: "Captura sin Conexin!",
            });
          }

          // NO HAY PENDIENTES
          if (!localStorage.getItem("dataOffline")) {
            localStorage.setItem("dataOffline", JSON.stringify([data]));

            Toast.fire({
              icon: "success",
              title: "Captura sin Conexin!",
            });
          }
        }
      } catch (error) {
        store.state.conexion = false;
      }
    };

    return { capturar };
  },
};
</script>
